import {StringResponse} from '../../Model/Response/StringResponse';
import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileLoopListResponse } from "../../Model/Response/FileLoopListResponse";
import { FileLoopResponse } from "../../Model/Response/FileLoopResponse";
import { FileLoopViewModel } from '../../Model/ViewModel/FileLoopViewModel';
import { NumberResponse } from '../../Model/Response/NumberResponse';

/*
  Generated class for the FileLoopServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileLoopServicesProvider {
  private FileLoops: FileLoopListResponse;
  private FileLoop: FileLoopResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FileLoop";
    console.log("Hello FileLoopServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
     sub_url = "FileLoop?PageSize={1}&PageNumber={2}&keyword={3}&exec_file_seq={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_file_seq,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileLoops = new FileLoopListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileLoops;
    });
  }
  public GetMaxExecSeqAsync(exec_file_seq:number) {
    var sub_url: string;
    sub_url = "FileLoop/get_max_exec_seq?exec_file_seq={1}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public PostPutAsync(FileLoopViewModel:FileLoopViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileLoop";
    else
    sub_url = "FileLoop/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileLoopViewModel.loop_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileLoopViewModel,mode).map(
      item => {
        this.FileLoop = new FileLoopResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.FileLoop;
      }
    );
  }

  public DeleteAsync(loop_seq:number) {
    var sub_url: string;
    sub_url = "FileLoop/{1}";
    sub_url = String.Format(sub_url, this.ctl,loop_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.FileLoop = new FileLoopResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileLoop;
      }
    );
  }

  public GetSqlAsync(loop_seq:number,method:string) {
    var sub_url: string;
    sub_url = "FileLoop/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,loop_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



