import { FileLoopServicesProvider } from "../../providers/file-loop-services/file-loop-services";
import { GlobalComponent } from "../../components/global/global";
import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileLoopViewModel } from "../../Model/ViewModel/FileLoopViewModel";

/**
 * Generated class for the FileLoopModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-loop-add-edit-modal",
  templateUrl: "file-loop-add-edit-modal.html"
})
export class FileLoopAddEditModalPage {
  item: FileLoopViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  FileLoop_type: string;
  change_mode:string;
  file_type_key:string;
  sql_help:string=`
LC01變數說明：
  依設定值取代{LC01}變數
  若第一碼為~表該設定值為檔案關鍵字的排除條件
  若第一碼為@表該設定值為檔案關鍵字的不排除條件
  若第一碼為!表該設定值為http上傳檔案時用到的FormData(001|FMDATA) 參數中的key
  若第一碼為#表該設定值為http Header 中的key
LC02變數說明：
  依設定值取代{LC02}變數
  若LC01第一碼為!表該設定值為http上傳檔案時用到的FormData 參數中的value
  若LC01第一碼為#表該設定值為http Header 中的Value
  `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private FileLoopServices: FileLoopServicesProvider,
    public loadingCtrl: LoadingController,
    private global: GlobalComponent,
    public modalCtrl: ModalController
  ) {

    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;
    this.file_type_key=navParams.data.file_type_key;
    this.item = new FileLoopViewModel();
    this.item.exec_file_seq=navParams.data.item.exec_file_seq;
    if(this.mode=="PUT")
      this.item.exec_seq=navParams.data.item.exec_seq;
    else
      this.item.exec_seq=navParams.data.totalRows+1;
    if(this.mode=="PUT")
      this.item.loop_seq=navParams.data.item.loop_seq;
    else
      this.item.loop_seq=this.item.exec_file_seq*100+this.item.exec_seq;

    this.item.lc01=navParams.data.item.lc01;
    this.item.lc02=navParams.data.item.lc02;
    this.item.lc03=navParams.data.item.lc03;

    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;

    if (this.change_mode!="e") {
      this.get_max_exec_seq();
    }

    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title=String.Format("{0}", this.item.exec_file_seq);
  }

  Help() {

    this.global.createLoader();

    this.global.loading.present().then(() => {
       this.global.showMessage("回圈變數說明",this.sql_help);

    });

  }

  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.FileLoopServices.GetMaxExecSeqAsync(
        this.item.exec_file_seq
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.item.loop_seq=this.item.exec_file_seq*100+this.item.exec_seq;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }


  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileLoopModalPage");
  }
}



