export class FileLoopViewModel {
  /// <summary>
  ///  迴圈序號
  /// <summary>
  public loop_seq: number;
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  迴圈欄位一
  /// <summary>
  public lc01: string;
  /// <summary>
  ///  迴圈欄位二
  /// <summary>
  public lc02: string;
  /// <summary>
  ///  迴圈欄位三
  /// <summary>
  public lc03: string;
  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
}



